.product_grid {
  list-style-type: none;
  margin: 0;
  padding: 0;
  @media screen and (min-width: 1200px) {
    grid-template-columns: repeat(5, minmax(0, 1fr));
  }
  @media screen and (min-width: 1700px) {
    grid-template-columns: repeat(6, minmax(0, 1fr));
  }
  @media screen and (min-width: 2100px) {
    grid-template-columns: repeat(7, minmax(0, 1fr));
  }
  @media screen and (min-width: 2400px) {
    grid-template-columns: repeat(8, minmax(0, 1fr));
  }
  &.centered_grid {
    max-width: 1440px;
    margin: 0 auto;
    gap: 0;
    @media screen and (min-width: 1200px) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
}
.product_card {
  position: relative;
  .sample_time {
    display: none;
    visibility: hidden;
  }
  .nohoverboards & {
    .sample_time {
      display: block;
      visibility: visible;
      .icon-swatch {
        width: 24px;
        height: 24px;
      }
    }
  }
  .hover & {
    .sample_time {
      display: block;
      visibility: visible;
      position: absolute;
      z-index: -1;
      opacity: 0;
      transition: var(--transition);
      width: 168px;
      padding: 0 4px;
      height: 52px;
      left: calc(50% - 84px);
      top: calc(50% - 26px);
      background-color: rgba(255,255,255,0.85);
      @media screen and (min-width: 1024px) {
        height: 64px;
      }
      &:hover {
        background-color: rgba(255,255,255,1);
      }
    }
    &:hover {
      .sample_time {
        z-index: 4;
        opacity: 1;
      }
    }
  }
  
  .badge {
    position: absolute;
    top: -4px;
    left: -4px;
    z-index: 5;
  }
  .flex {
    position: relative;
    z-index: 1;
    gap: 8px;
    .text {
      text-align: center;
      order: 2;
      flex: 1 0 100%;
      .price {
        margin-top: 4px;
        font-size: 75%;
        dl {
          flex: 1 0 100%;
        }
        text-align: center;
        .saving {
          opacity: 0.7;
          margin-left: 16px;
        }
        &.price--sold-out {
          opacity: 0.5;
        }
      }
      .soldout {
        margin-top: 0.5;
        opacity: 0.5;
      }
      h3.h5 {
        font-family: var(--font-heading-family);
        margin: 0;
        color: var(--color-base-text);
        font-size: 100%;
        a {
          color: var(--color-base-text);
        }
      }
    }
    .image {
      order: 1;
      flex: 1 0 100%;
      img {
        display: block;
      }
    }
  }

}

